import React, { createRef, useState } from 'react'
import { Message } from 'semantic-ui-react'
import './alert.scss'
export default function Alert({ variation, timeout, header, content, id, deleteAlert }) {

    const [showing, setShowing] = useState(true)

    const variations = {
        'error': false,
        'info': false,
        'warning': false,
        'positive': false
    }

    variations[variation] = true

    const myRef = createRef();

    function closeAlert() {
        const el = myRef.current
        el.className = 'fade-out'
        deleteAlert(id)

    }

    if (timeout) {
        setTimeout(() => {
            closeAlert()
        }, Math.abs(timeout))
    }

    return (
        <div className='alertContainer' ref={myRef}>
            <Message error={variations.error} positive={variations.positive} info={variations.info} warning={variations.warning} floating onDismiss={closeAlert} header={header} content={content} />
        </div>
    )
}
