import React, { useRef } from 'react'
import Alert from '../../components/alert/alert'
import './alertsProvider.scss'
import { useState } from 'react'
import { v4 as uuid } from 'uuid'
const AlertContext = React.createContext({})


export function AlertsProvider(props) {
    const [alerts, setAlerts] = useState([])
    function addAlert({ header, timeout, content, variation }) {
        alert = {
            header: header,
            timeout: timeout,
            content: content,
            variation: variation,
            id: uuid()
        }
        setAlerts([...alerts, alert])
    }
    function deleteAlert(id) {
        const newAlerts = alerts.filter(alert => alert.id !== id)
        setAlerts(newAlerts)
    }

    return (
        <AlertContext.Provider value={{ alerts: alerts, addAlert: addAlert }}>
            {props.children}
            <div className="alertsContainer">
                {alerts.map(function (alert, i) {
                    return <Alert key={alert.id} id={alert.id} deleteAlert={deleteAlert} header={alert.header} timeout={alert.timeout} content={alert.content} variation={alert.variation} />
                })}
            </div>

        </AlertContext.Provider>
    )
}

export const useAlertsContext = () => React.useContext(AlertContext)