import React, { useState } from 'react'
import { Container, Header, Button, Divider, Table, Input, Menu, Segment, Icon } from 'semantic-ui-react'

const ClientContext = React.createContext()

export const useClientContext = () => React.useContext(ClientContext)

export default function ClientComponent(props) {
    const [clientData, setClientData] = useState({})
    const [activeTab, setActiveTab] = useState('Client')
    function handleItemClick(e, { name }) {
        setActiveTab(name)
    }
    return (
        // <ClientContext.Provider value={{ clientData }}>
        //     {props.children}
        // </ClientContext.Provider>
        <div>
            <div className="header-container">
                <h2>{props.currentClient.name}</h2>
                <Button color='blue' animated onClick={props.goToOverview}>
                    <Button.Content visible>Back</Button.Content>
                    <Button.Content hidden>
                        <Icon name='arrow left' />
                    </Button.Content>
                </Button>
            </div>
            <Menu attached='top' tabular>
                <Menu.Item
                    name='Client'
                    active={activeTab === 'Client'}
                    onClick={handleItemClick}
                />
                <Menu.Item
                    name='Intake'
                    active={activeTab === 'Intake'}
                    onClick={handleItemClick}
                />
                <Menu.Item
                    name='Initial Proposal'
                    active={activeTab === 'Initial Proposal'}
                    onClick={handleItemClick}
                />
                <Menu.Item
                    name='Session Notes'
                    active={activeTab === 'Session Notes'}
                    onClick={handleItemClick}
                />
                <Menu.Item
                    name='Road Map'
                    active={activeTab === 'Road Map'}
                    onClick={handleItemClick}
                />
                <Menu.Item
                    name='Whiteboard'
                    active={activeTab === 'Whiteboard'}
                    onClick={handleItemClick}
                />
                <Menu.Item
                    name='Timesheet'
                    active={activeTab === 'Timesheet'}
                    onClick={handleItemClick}
                />
            </Menu>
            <Segment attached='bottom'>
                <img src='/images/wireframe/paragraph.png' />
            </Segment>
        </div>
    )
}
