import React, { useContext, useRef } from 'react'
import { getAuth, signInWithEmailAndPassword } from 'firebase/auth'
import { Button, Checkbox, Container, Form, Header } from 'semantic-ui-react';
import { useState } from 'react'
import { auth } from '../../firebase'
import { UserContext } from '../../App';
import { useNavigate } from "react-router-dom"
import './login.scss'
import { AlertsProvider, useAlertsContext } from '../../providers/alerts/alertsProvider';
export default function Login() {
    const alertsProvider = useAlertsContext()
    const { setUserDetails } = useContext(UserContext)
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")

    const onSubmit = async (event) => {
        event.preventDefault();
        signInWithEmailAndPassword(auth, email, password)
            .then((userCredential) => {
                const newUser = userCredential.user;
                setUserDetails(newUser)
                alertsProvider.addAlert({ content: "Logged in succesfully!", timeout: 2000, variation: 'positive' })
            })
            .catch((error) => {
                const errorCode = error.code;
                const errorMessage = error.message;
                alertsProvider.addAlert({ header: errorCode, content: errorMessage, variation: 'error', timeout: 2000 })
            })
    }
    return (
        <div>
            <Container text className='container'>
                <Header as="h2">Sign in</Header>
                <Form onSubmit={onSubmit}>
                    <Form.Field>
                        <label >Email</label>
                        <input maxLength={50} type="text" placeholder='Email' onChange={(e) => setEmail(e.target.value)} />
                    </Form.Field>
                    <Form.Field>
                        <label >Password</label>
                        <input maxLength={100} type="password" placeholder='password' onChange={(e) => setPassword(e.target.value)} />
                    </Form.Field>
                    <Form.Button content='Sign in' />
                </Form>
            </Container>
        </div>
    )
}
