import React from 'react'
import "./navbar.scss"
import logo from "../../assets/logo.png"
import { useUserContext } from '../../App'
import { Button } from 'semantic-ui-react'
import { auth } from '../../firebase'
import { useNavigate } from 'react-router-dom'
import { useAlertsContext } from '../../providers/alerts/alertsProvider'
import { useUserDataContext } from '../../providers/UserData/UserDataProvider'


export default function Navbar() {
  const alertsProvider = useAlertsContext()
  const { role } = useUserDataContext()
  const { userDetails, setUserDetails } = useUserContext()
  const nav = useNavigate()


  async function logOut() {
    auth.signOut().then(() => {
      setUserDetails(false)
      alertsProvider.addAlert({ content: "Logged out succesfully!", timeout: 2000, variation: 'positive' })
    }
    ).catch((error) => {
      const errorCode = error.code;
      const errorMessage = error.message;
      alertsProvider.addAlert({ header: errorCode, content: errorMessage, variation: 'error', timeout: 2000 })
    })
    nav('/')
  }



  return (
    <div>
      <div className="navbar">
        {role ? <div>Role: {role}</div> : <div></div>}

        <img src={logo} alt="" onClick={() => nav('/')} />
        {userDetails.uid ? <div className="userInfo">Logged in as {userDetails.email}<Button content={"Log out"} onClick={logOut} /> </div> :
          <div></div>}
      </div>

    </div>
  )
}
