import React, { useContext, useState } from 'react'
import { Container, Header, Button, Divider, Table, Input, Menu, Segment, Icon } from 'semantic-ui-react'

import { useNavigate } from 'react-router-dom'
import { useUserDataContext } from '../../providers/UserData/UserDataProvider'

export function OverviewComponent(props) {
    const { clients } = useUserDataContext()
    const [search, setSearch] = useState('')
    const navigate = useNavigate()

    function addNewUser() {
        navigate('/addUser')
    }

    function customFilter(client) {
        console.log(client.name)
        if (client.name.includes(search) || client.companyName.includes(search) || client.email.includes(search)) {
            return true
        }
        return false
    }

    return (
        <div>

            <div className="header-container">
                <h2>Clients</h2>
                <div className="right">
                    <Input placeholder='Search...' onChange={(e) => setSearch(e.target.value)} />
                    <Button color='blue' onClick={addNewUser} >Add User </Button>
                </div>
            </div>
            <Table singleLine selectable>
                <Table.Header >
                    <Table.Row>
                        <Table.HeaderCell>Name</Table.HeaderCell>
                        <Table.HeaderCell>Email</Table.HeaderCell>
                        <Table.HeaderCell>Company</Table.HeaderCell>
                    </Table.Row>
                </Table.Header>
                <Table.Body>
                    {clients ? clients.filter(client => customFilter(client)).map((client) => {
                        return (<Table.Row key={client.id} onClick={() => props.goToClient(client)}>
                            <Table.Cell>{client.name}</Table.Cell>
                            <Table.Cell>{client.email}</Table.Cell>
                            <Table.Cell>{client.companyName}</Table.Cell>
                        </Table.Row>)
                    }) : <Table.Row></Table.Row>}
                </Table.Body>

            </Table>
        </div>
    )
}
