import React, { useEffect, useState } from 'react'
import { Container, Header, Button, Divider, Table, Input, Menu, Segment, Icon } from 'semantic-ui-react'
import { useNavigate } from 'react-router-dom'
import './home.scss'
import { useUserDataContext } from '../../providers/UserData/UserDataProvider'
import { OverviewComponent } from '../../components/overview/overview'
import ClientComponent from '../../components/clients/clients'
export default function Home() {
    const { clients, role } = useUserDataContext()
    const navigate = useNavigate()

    const [currentClient, setCurrentClient] = useState()
    const [state, setState] = useState('overview')

    function goToClient(client) {
        setCurrentClient({ id: client.id, name: client.name })
        setState('client')
    }
    function goToOverview() {
        setCurrentClient({})
        setState('overview')
    }
    return (
        <div className='home'>
            <Container >
                {role === 'admin' ? state === 'overview' ? <OverviewComponent goToClient={goToClient} /> :
                    <ClientComponent currentClient={currentClient} goToOverview={goToOverview}></ClientComponent>
                    : <div></div>}
            </Container>
        </div>
    )
}
