import React, { useState, useContext, useEffect } from 'react'
import { UserContext, useUserContext } from '../../App';
import { auth, db } from '../../firebase'
import { doc, getDoc, where, collection, getDocs, query } from 'firebase/firestore'
import { AlertsProvider, useAlertsContext } from '../alerts/alertsProvider';
export const UserDataContext = React.createContext([])

export const useUserDataContext = () => React.useContext(UserDataContext)

export function UserDataProvider(props) {
    const { userDetails } = useUserContext()
    const [role, setRole] = useState(false)
    const [clients, setClients] = useState([])


    async function getRole() {
        if (userDetails) {
            const docRef = doc(db, 'users', userDetails.uid)
            const docSnap = await getDoc(docRef)
            if (docSnap.exists()) {
                const data = docSnap.data()
                const newRole = data['role']
                if (newRole) {
                    setRole(newRole)
                }
            } else {

            }
        }
    }
    async function getClients() {
        const q = query(collection(db, 'users'), where('role', '==', 'client'))
        const querySnapshot = await getDocs(q)
        setClients([])
        querySnapshot.forEach((doc) => {
            const data = doc.data()
            console.log(data.name)

            data.id = doc.id
            // doc.data() is never undefined for query doc snapshots
            // console.log(doc.id, " => ", doc.data());
            setClients(clients => (
                [...clients, data]
            ))
        });
        console.log(clients)
    }
    useEffect(() => {
        getRole()
        console.log(clients)

    });
    useEffect(() => {
        getClients()
    }, [])

    return (

        <UserDataContext.Provider value={{ role, setRole, clients }}>{props.children}
        </UserDataContext.Provider>
    )
}
