import logo from './logo.svg';
import './App.scss';
import Login from './pages/login/login';
import Navbar from './components/navbar/navbar';
import React, { useEffect } from 'react';
import { createContext, useState } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Dashboard from './pages/home/home';
import Register from './pages/register/register';
import { auth } from './firebase'
import { UserDataProvider } from './providers/UserData/UserDataProvider';
import { AlertsProvider, useAlertsContext } from './providers/alerts/alertsProvider'
const UserContext = React.createContext({});

function App() {
  const [userDetails, setUserDetails] = useState(false)

  useEffect(() => {
    auth.onAuthStateChanged((user) => {
      if (user) {
        setUserDetails(user)

      } else {
        setUserDetails(false)
      }
    })
  })
  const isAuthenticated = () => {
    if (userDetails) {
      return true
    }
    return false
  }
  return (
    <UserContext.Provider value={{ userDetails, setUserDetails }}>

      <AlertsProvider className="App">
        <UserDataProvider >
          <BrowserRouter>
            <Navbar />
            <div className='main'>
              <Routes>
                <Route path="/" element={isAuthenticated() ? <Dashboard /> : <Login />} />
                <Route path="/addUser" element={isAuthenticated() ? <Register /> : <Login />} />
              </Routes>
            </div>
          </BrowserRouter>
        </ UserDataProvider>
      </AlertsProvider>

    </UserContext.Provider >
  );
}
export const useUserContext = () => React.useContext(UserContext)
export { UserContext }
export default App;
